import React, { useState } from 'react'

import styles from './Styles/Header.module.css';
import FilterAdmin from './FilterAdmin';
import SortingField from './SortingField';
import filter from '../../Media/filter-solid.svg'

interface Props {

}

const Header: React.FC<Props> = () => {
    const [filtersOpen, setFiltersOpen] = useState(false);

    return (
        <div className={styles.wrapper}>
            <div className={styles.inside}>
                <div className={styles.logo}>
                    Ã¸lPol
                </div>
                <div className={styles.spacer}></div>
                {filtersOpen && <FilterAdmin />}
                <SortingField slimview={!filtersOpen} />
                <button className={styles.filterButton} onClick={() => setFiltersOpen(!filtersOpen)} >
                    <img alt={"Filter icon"} src={filter} />
                </button>
            </div>
        </div>
    )
}

export default Header
